/* Checkbox */
.gkb-checkbox {
  z-index: 0;
  position: relative;
  display: inline-block;
  color: #3f4b60;
  font-family: "Roboto";
  font-size: 16px;
  line-height: 1.5;
}

/* Input */
.gkb-checkbox > input {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  z-index: -1;
  position: absolute;
  margin: auto;
  left: -7px;
  top: 0;
  bottom: 0;
  display: block;
  border-radius: 4px;
  width: 40px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0.6);
  background-color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), 0.6);
  box-shadow: none;
  outline: none;
  opacity: 0;
  -webkit-transform: scale(1);
          transform: scale(1);
  pointer-events: none;
  transition: opacity 0.3s, -webkit-transform 0.2s;
  transition: opacity 0.3s, transform 0.2s;
  transition: opacity 0.3s, transform 0.2s, -webkit-transform 0.2s;
}

/* Span */
.gkb-checkbox > label {
  display: flex;
  align-items: center;
  width: 100%;
  cursor: pointer;
}

/* Box */
.gkb-checkbox > label::before {
  content: "";
  display: inline-block;
  box-sizing: border-box;
  margin: 3px 16px 3px 1px;
  border: 1px solid #3f4b60;
  border-radius: 4px;
  width: 24px;
  height: 24px;
  vertical-align: top;
  transition: border-color 0.2s, background-color 0.2s;
}

/* Checkmark */
.gkb-checkbox > label::after {
  content: "";
  display: block;
  position: absolute;
  margin: auto;
  top: -12px;
  bottom: 0;
  left: 3px;
  width: 14px;
  height: 7px;
  border: solid 2px transparent;
  border-right: none;
  border-top: none;
  -webkit-transform: translate(3px, 4px) rotate(-45deg);
          transform: translate(3px, 4px) rotate(-45deg);
}

/* Checked, Indeterminate */
.gkb-checkbox > input:checked,
.gkb-checkbox > input:indeterminate {
  background-color: #eaf6f5;
}

.gkb-checkbox > input:checked + label::before,
.gkb-checkbox > input:indeterminate + label::before {
  border-color: #20b5be;
  background-color: #20b5be;
}

.gkb-checkbox > input:checked + label::after,
.gkb-checkbox > input:indeterminate + label::after {
  border-color: white;
}

.gkb-checkbox > input:indeterminate + label::after {
  border-left: none;
  -webkit-transform: translate(4px, 3px);
          transform: translate(4px, 3px);
}

/* Hover, Focus */
.gkb-checkbox:hover > input {
  opacity: 0.04;
}

.gkb-checkbox > input:focus {
  opacity: 0;
}

.gkb-checkbox:hover > input:focus {
  opacity: 0.04;
}

/* Active */
.gkb-checkbox > input:active {
  opacity: 1;
  -webkit-transform: scale(0);
          transform: scale(0);
  transition: opacity 0s, -webkit-transform 0s;
  transition: transform 0s, opacity 0s;
  transition: transform 0s, opacity 0s, -webkit-transform 0s;
}

.gkb-checkbox > input:active + label::before {
  border-color: #eaf6f5;
}

.gkb-checkbox > input:checked:active + label::before {
  border-color: transparent;
  background-color: rgba(0, 0, 0, 0.6);
  background-color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), 0.6);
}

/* Disabled */
.gkb-checkbox > input:disabled {
  opacity: 0;
}

.gkb-checkbox > input:disabled + label {
  color: rgba(0, 0, 0, 0.38);
  color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), 0.38);
  cursor: auto;
  cursor: initial;
}

.gkb-checkbox > input:disabled + label::before {
  border-color: currentColor;
}

.gkb-checkbox > input:checked:disabled + label::before,
.gkb-checkbox > input:indeterminate:disabled + label::before {
  border-color: transparent;
  background-color: currentColor;
}

